body {
  font-family: $montserrat-font;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
p {
  margin: 0;
  padding: 0;
  list-style: none;
}

// .list {
//   list-style: none;
// }

// .link {
//   text-decoration: none;
// }

ul {
  list-style: none;
}

a {
  text-decoration: none;
  z-index: 999;
}

button {
  cursor: pointer;
  z-index: 999;
}


img {
  display: block;
  max-width: 100%;
  height: auto;
}

table,
tbody,
caption,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-style: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

.container {
  max-width: 414px;
  margin: 0 auto;
  padding: 0 15px;
  @media screen and (min-width: $tablet) {
    max-width: 1024px;
    padding: 0 47px;
  }

  @media screen and (min-width: $desktop) {
    max-width: 1280px;
    padding: 0 85px;
  }
}

.modal {
  z-index: 9999;
}